import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import * as moment from 'moment';

import { ExpenseService } from './../../../core/services/expense.service';
import { SessionService } from './../../../core/services/session.service';
import { CreateRequestService } from './../../../core/services/create-request.service';

import { FileSizeValidator } from '@app/modules/shared/directives/file-size-validator.directive';
import { CurrenciesService } from './../../../core/services/references/currencies.service';
import { ExpenseCategoriesService } from './../../../core/services/references/expense-categories.service';
import { PaymentMethodsService } from './../../../core/services/references/payment-methods.service';
import { FileValidator } from '@app/modules/shared/directives/file-validator.directive';

import { LangService } from './../../../core/services/references/lang.service';
import { combineLatest } from 'rxjs';


@Component({
  selector: 'itx-helpdesk-edit',
  templateUrl: './expense-edit.page.html',
  styleUrls: ['./expense-edit.page.scss']
})
export class ExpenseEditPage implements OnInit, OnDestroy {

  public translation;

  public requestId: number;
  public request: any;
  public requestMessages: any;
  public userInfos;

  public completeRequest: boolean = false;
  public requestStatus: string;
  public requestStatusClass: string = 'shell';

  public helpdeskForm: FormGroup;
  public response: FormControl;
  public file: FormControl;

  public expenseCategory;
  public currency;
  public paymentMethod;

  private combinedExpenseByIdAndMessages;
  private combinedExpenseByIdAndMessagesObs;

  private attachments: any;
  public base64Files: any[] = [];
  private sendRequestSubscription;

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private route: ActivatedRoute,
    private createRequestService: CreateRequestService,
    private sessionService: SessionService,
    private expenseService: ExpenseService,
    private currenciesService: CurrenciesService,
    private expenseCategoriesService: ExpenseCategoriesService,
    private paymentMethodsService: PaymentMethodsService,
    private langService: LangService,
    private sanitizer: DomSanitizer
  ) {
    this.translation = this.langService.lang;

    this.response = new FormControl(null, [Validators.required]);
    this.file = new FormControl(null, [ FileSizeValidator.validate ]);
    this.helpdeskForm = this.fb.group({
      response : this.response,
      file: this.file
    });

    this.route.params.subscribe(params => {
      this.requestId = params['id'];
    });

    this.sessionService.userInfos$.subscribe(res => {
      this.userInfos = res;
    });

    this.combinedExpenseByIdAndMessages = combineLatest(
      this.expenseService.expenseById$,
      this.expenseService.expenseMessages$
    );

    this.combinedExpenseByIdAndMessagesObs = this.combinedExpenseByIdAndMessages.subscribe( ([expenseById, expenseMessages]) => {
      this.request = expenseById;
      this.requestMessages = expenseMessages;
      this.setStatus();
      this.getExpenseEvent();
      this.getExpenseCat(this.request.ExpenseInfo.Category);
      this.getCurrency(this.request.ExpenseInfo.Currency);
      this.getPaymentMethod(this.request.ExpenseInfo.PaymentMethod);
      if (this.request.AttachmentFiles) {
        this.getAttachedFiles();
      }
      const isEditMode: boolean = this.request.Status.ID === 2 ? true : false;
      this.createRequestService.editRequestView(true, isEditMode);
    });

    this.sendRequestSubscription = this.createRequestService.sendRequest$.subscribe(res => {
      if (this.helpdeskForm.valid) {
        this.addMessage();
      } else {
        this.markAllDirty(this.helpdeskForm);
      }
    });
  }

  ngOnInit(): void {
    this.loadData();
    this.sessionService.getUserInfos();
  }

  ngOnDestroy(): void {
    this.createRequestService.createRequest(false);
    this.createRequestService.editRequestView(false, false);

    this.combinedExpenseByIdAndMessagesObs.unsubscribe();
    this.sendRequestSubscription.unsubscribe();
  }

  private loadData(): void {
    this.base64Files = [];
    this.expenseService.getExpenseRequestsById(this.requestId);
    this.expenseService.getExpenseRequestsMessages(this.requestId);
  }

  private markAllDirty(control: AbstractControl): void {
    if (control.hasOwnProperty('controls')) {
        control.markAsDirty();
        const ctrl: any = <any>control;
        for (const inner in ctrl.controls) {
          if (ctrl.controls[inner]) {
            this.markAllDirty(ctrl.controls[inner] as AbstractControl);
          }
        }
    } else {
        (<FormControl>(control)).markAsDirty();
    }
  }

  public getAttachedFiles(): void {
    this.request.AttachmentFiles.forEach(file => {
      if (file.Type === 'JPG' || file.Type === 'jpg' || file.Type === 'jpeg' || file.Type === 'png') {
        this.getFile(file);
      } else {
        this.reorganizeFilesAndMessages({
          base64: null,
          type: file.Type,
          name: file.Name,
          date: file.Date
        });
      }
    });
  }

  public getExpenseEvent(): void {

  }

  public getExpenseCat(id: number): void {
    this.expenseCategoriesService.getExpenseCategoriesById(id).subscribe(res => {
      this.expenseCategory = res;
    });
  }

  public getCurrency(code: string): void {
    this.currenciesService.getCurrenciesByCode(code).subscribe(res => {
      this.currency = res;
    });
  }

  public getPaymentMethod(id: number): void {
    this.paymentMethodsService.getPaymentMethodsById(id).subscribe(res => {
      this.paymentMethod = res;
    });
  }

  private reorganizeFilesAndMessages(file) {
    let pushed: boolean = false;
    for (let i = 0; i < this.requestMessages.length; i++) {
      const message = this.requestMessages[i];
      const messageDate = moment(message.Date);
      const fileDateUp = moment(file.date).add(5, 's');
      const fileDateDown = moment(file.date).subtract(5, 's');
      if (moment(messageDate).isBetween(fileDateDown, fileDateUp, 'seconds')) {
        if (!message.file) { message.file = []; }
        message.file.push(file);
        pushed = true;
        break;
      }
    }
    if (!pushed) {
      this.base64Files.push(file);
    }
  }

  public getFile(file): void {
    this.expenseService.getAttachmentFile64(this.requestId, file.Name).subscribe(res => {
      this.reorganizeFilesAndMessages({
        base64: this.sanitizer.bypassSecurityTrustResourceUrl('data:image/png;base64, ' + res._body),
        type: 'img',
        name: file.Name,
        date: file.Date
      });
    });
  }

  addMessage(): void {
    this.createRequestService.requestInProgress(true);
    this.expenseService.addExpenseRequestsMessage(this.requestId, this.response.value, this.attachments).subscribe(res => {
      this.createRequestService.requestInProgress(false);
      // this.loadData();
      this.router.navigate(['/']);
    });
  }

  public amIAuthor(message: any): boolean {
    let iAmAuthor: boolean;
    if (message.Author === this.userInfos.data.UserInfo.DisplayName) {
      iAmAuthor = true;
    } else {
      iAmAuthor = false;
    }
    return iAmAuthor;
  }

  private setStatus(): void {
      if (this.request.Status.ID === 1) {
        this.completeRequest = false;
        this.requestStatus = this.translation.requestStatus.created || 'created';
        this.requestStatusClass = 'created';
      } else if (this.request.Status.ID === 2) {
        this.completeRequest = true;
        this.requestStatus = this.translation.requestStatus.pending || 'Pending';
        this.requestStatusClass = 'pending';
        this.createRequestService.createRequest(true);
      } else if (this.request.Status.ID === 3) {
        this.completeRequest = false;
        this.requestStatus = this.translation.requestStatus.inProgress || 'In Progress';
        this.requestStatusClass = 'inprogress';
      } else if (this.request.Status.ID === 4) {
        this.completeRequest = false;
        this.requestStatus = this.translation.requestStatus.completed || 'Completed';
        this.requestStatusClass = 'completed';
      } else {
        this.completeRequest = false;
        this.requestStatus = this.translation.requestStatus.closed || 'Closed';
        this.requestStatusClass = 'closed';
      }
  }

  public getFilesToUpload(files: any): void {
    this.attachments = files;
  }

}
